.group-stage-group-match {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-top: 5px;
    width: 400px;
    background-color: #D9D9D9;
    padding-top: 2px;
    border-radius: 5px;
}
.group-stage-group-match > p {
    margin-top: 2px;
}
.group-stage-group-match-countries {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: #F9F4F5;
    width: 100%;
    border-radius: 5px;
    padding: 3px;
}
.group-stage-group-match-team > p{
    font-size: 13px;
    width: 100%;
    text-align: center;
}
.group-stage-group-match-countries-flag {
    height: 42px;
    width: 42px;
    border-radius: 42px;
}

.group-stage-group-match-date {
    font-size: 10px;
    font-weight: bold;
}